<template lang="pug">
    section(class="plans")
        div(class="container")
            div(class="title")
                h2 {{ $t("plans") }} 
                    span {{ $t("host") }}
            div(class="plans-content")
                div(class="row")
                    div(class="col-md-6 col-xl-3 box" v-for="plan in PlansData.plans", :key="plan.id")
                        div(class="plans-box box wow fadeInUp" data-wow-duration="1s")
                            .plans-type 
                                h2 {{ plan.title }}
                            .plans-price 
                                h3 {{ plan.price }}
                                span.per {{ $t("per-year") }} 
                            .plans-details(v-html="plan.body")
</template>

<script>
import { mapState } from "vuex";

export default {
    name: "Plans",
    metaInfo() {
        return {
            title: process.env.VUE_APP_TITLE,
            titleTemplate: `%s | ${this.PlansData.website_title}`,
            meta: [
                { charset: "utf-8" },
                {
                    name: "description",
                    content: this.PlansData.meta_description,
                },
                {
                    name: "keywords",
                    content: this.PlansData.meta_keywords,
                },
            ],
        };
    },
    mounted() {
        this.$store.dispatch("getPlansData");
    },
    computed: {
        ...mapState(["PlansData"]),
    },
};
</script>
